<template>
    <div>
        <div ref="map"></div>
    </div>
</template>

<script>
    import { Scene } from '@antv/l7';
    import { CountryLayer } from '@antv/l7-district';
    import { GaodeMap } from '@antv/l7-maps';
    import axios from 'axios'

    const colors = [ '#f3f3f3', '#fff2cf', '#fdd2a0', '#ff8c71', '#e64b45', '#bd1316', '#7f1100' ];
    export default {
        name: "country",
        data(){
            return {
            //    放置地图根节点
                el: null,
            //    图层
                layer: null,
                //场景
                scene: null,
                ProvinceData: []
            }
        },
        created(){

            const _this = this;
            axios.get("http://127.0.0.1:1024/getCovid19Data").then(function (respones) {
                if (respones != null && respones.data.success == true){
                    _this.ProvinceData = respones.data.data.provinceArray;
                }
                else {
                    _this.$message.error('数据加载失败,请重试');
                }
            })
            this.$nextTick(() => {
                this.el = this.$refs.map;
                this.scene = new Scene({
                    id: this.el,//id 属性，需要填入一个地图根节点的 css 选择器或者一个节点元素
                    logoVisible: false,
                    map: new GaodeMap({
                        center: [ 116.2825, 39.9 ],
                        pitch: 0,  //仰角角度
                        style: 'light',
                        zoom: 3.7,    //缩放
                        minZoom: 3.7, //最小缩放
                        maxZoom: 5.7 //最大缩放
                    })
                });

            //    注册场景加载完成事件
                this.scene.on('loaded', () => {
                    this.layer = new CountryLayer(this.scene, {
                        data: this.ProvinceData,
                        joinBy: [ 'NAME_CHN', 'childStatistic' ],
                        depth: 1,
                        provinceStroke: '#fff',
                        cityStroke: '#EBCCB4',
                        cityStrokeWidth: 1,
                        fill: {
                            color: {
                                field: 'totalCured',
                                values: (value) => {
                                    return value >= 10000 ? colors[6] :
                                        value >= 1000 ? colors[5] :
                                            value >= 500 ? colors[4] :
                                                value >= 100 ? colors[3] :
                                                    value >= 10 ? colors[2] :
                                                        value >= 1 ? colors[1] : colors[0]
                                }
                            }
                        },
                        stroke: '#fff',
                        label: {
                            enable: true,
                            textAllowOverlap: false,
                            opacity: 0.7
                        },
                        popup: {
                            enable: true,
                            Html: props => {
                                return `<span style="font-size: 18px;">${props.NAME_CHN}:</span><br/>
                                &nbsp;&nbsp;&nbsp;<span style="color: #fe7e07;">确诊人数：${props.totalConfirmed}</span><br/>
                                &nbsp;&nbsp;&nbsp;<span style = "color: #d31b29;">死亡人数：${props.totalDeath}</span><br/>
                                &nbsp;&nbsp;&nbsp;<span style="color: #3f8516;">治愈人数：${props.totalCured}</span>`;
                            }
                        }
                    });
                });
            })
        }
    }
</script>

<style scoped>

    body{
        margin: 0;
        padding: 0;
        overflow: hidden;
    }
    div{
        overflow: hidden;
    }

</style>